import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot, Routes } from '@angular/router';
import { Observable } from 'rxjs';
import { IgMediaService } from './ig-media.service';
import { IgMediaFull } from 'src/app/models/ig-media-model';
import { IgMediaComponent } from './ig-media.component';
const PREFIX = 'p';

@Injectable({ providedIn: 'root' })
export class IgMediaFullResolver implements Resolve<IgMediaFull> {
	constructor(private service: IgMediaService) { }

	resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IgMediaFull> {
		return this.service.GetIgMediaFullByShortcode(route.paramMap.get('shortcode'));
	}
}

export const igMediaRoutes: Routes = [
	{
		path: PREFIX + '/:shortcode',
		component: IgMediaComponent,
		resolve: {
			igMediaFull: IgMediaFullResolver
		}
	}
];