import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot, Routes } from '@angular/router';
import { Observable } from 'rxjs';
import { IgHashtagService } from './ig-hashtag.service';
import { IgHashtagFull } from 'src/app/models/ig-hashtag-model';
import { IgHashtagComponent } from './ig-hashtag.component';
const PREFIX = 'hashtag';

@Injectable({ providedIn: 'root' })
export class IgHashtagFullResolver implements Resolve<IgHashtagFull> {
	constructor(private service: IgHashtagService) { }

	resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IgHashtagFull> {
		return this.service.GetIgHashtagFullByHashtag(route.paramMap.get('hashtag'));
	}
}

export const igHashtagRoutes: Routes = [
	{
		path: PREFIX + '/:hashtag',
		component: IgHashtagComponent,
		resolve: {
			igHashtagFull: IgHashtagFullResolver
		}
	}
];