import { Component, OnInit } from '@angular/core';
import { IgTopSearchService } from './ig-topsearch.service';
import { Observable, of } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap, switchMap, catchError, map } from 'rxjs/operators';
import { IgUser } from 'src/app/models/ig-user-model';
import { IgHashtag } from 'src/app/models/ig-hashtag-model';
import { isNullOrUndefined } from 'util';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  model: any;
  searching = false;
  searchFailed = false;

  constructor(public igTopService: IgTopSearchService,
              public router: Router) { }

  ngOnInit(): void {
  }

  search = (text$: Observable<string>) =>
    text$.pipe(
      debounceTime(300),
      distinctUntilChanged(),
      tap(() => this.searching = true),
      switchMap(term => term.length < 3 ? of([]) :
        this.igTopService.GetIgTopSearch(term).pipe(
          tap(() => this.searchFailed = false),
          map(x => x.orderedMixedList),
          catchError(() => {
            this.searchFailed = true;
            return of([]);
          }))
      ),
      tap(() => this.searching = false)
    )

  igObjectFormatter = (obj: any) => {
    if (obj instanceof IgUser) {
      return obj.full_name + " (" + obj.username + ")";
    } else if (obj instanceof IgHashtag) {
      return "#" + obj.name + " (Media count: " + obj.media_count + ")";
    }

    return "?";
  }

  igObjectSelectedFormatted = (obj: any) => {
    if (obj instanceof IgUser) {
      return obj.full_name + " (" + obj.username + ")";
    } else if (obj instanceof IgHashtag) {
      return "#" + obj.name;
    }

    return "?";
  };

  isIgUser(obj) {
    if(isNullOrUndefined(obj)) return false;

    return obj instanceof IgUser;
  }

  isIgHashtag(obj) {
    if(isNullOrUndefined(obj)) return false;

    return obj instanceof IgHashtag;
  }

  isIgModelSelectedValid() {
    if(this.isIgUser(this.model) || this.isIgHashtag(this.model)) {
      return !isNullOrUndefined(this.model.id);
    }

    return false;
  }

  onSearch() {
    if(this.isIgUser(this.model)) {
      this.router.navigateByUrl("/" + this.model.username);
    } else if(this.isIgHashtag(this.model)) {
      this.router.navigateByUrl("/hashtag/" + this.model.name);
    }
  }
}
