import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { IgTopSearch } from 'src/app/models/ig-topsearch-model';
import deepCopy from '../../utils/deep-object-map';

@Injectable()
export class IgTopSearchService {
  URL = "/api/ig-topsearch/";

  constructor(private http: HttpClient) { }

  GetIgTopSearch(keyword: string) : Observable<IgTopSearch> {
      return this.http.get<IgTopSearch>(this.URL + keyword).pipe(
          map(res => new IgTopSearch(res)));
  }
}