import { IgMedia } from "./ig-media-model";

export class IgHashtagFull {
    id: number;
    name: string;
    profile_pic_url: string;
    related_media: IgMedia[] = [];
    media_count: number;
    correlated_hashtags: string[] = [];

    public constructor(obj?: any) {
        Object.assign(this, obj);
        this.related_media = this.related_media.map(x => Object.assign(new IgMedia(), x));
    }
}

export class IgHashtag {
    id: number;
    name: string;
    media_count: number;
    profile_pic_url: string;
    position: number;
}