import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IgUserFull } from 'src/app/models/ig-user-model';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
export class IgUserService {
  URL = "/api/ig-user/";
  constructor(private http: HttpClient) { }

  GetIgUserByUsername(username: string) : Observable<IgUserFull> {
      return this.http.get<IgUserFull>(this.URL + username).pipe(
          map(res => Object.assign(new IgUserFull(), res)));
  }
}