export class IgMediaFull {
    id: number;
    type: IgMediaType;
    shortcode: string;
    timestamp: number;
    dimensions: { width: number, height: number } = { width: 0, height: 0 };
    display_url: string;
    video_url: string;
    media_list: string[] = [];
    caption: string;
}

export class IgMedia {
    id: number;
    type: IgMediaType;
    shortcode: string;
    timestamp: number;
    dimensions: { width: number, height: number } = { width: 0, height: 0 };
    thumbnail_src: string;
    caption: string;
}

export enum IgMediaType {
    GraphSidecar = "GraphSidecar",
    GraphImage = "GraphImage",
    GraphVideo = "GraphVideo"
};