import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IgMediaService } from '../ig-media/ig-media.service';
import { ModalService } from '../modal/full-media/modal.service';
import { IgHashtagFull } from 'src/app/models/ig-hashtag-model';
import { IgMedia } from 'src/app/models/ig-media-model';
import { FullMediaComponent } from '../modal/full-media/full-media.component';

@Component({
  selector: 'app-ig-hashtag',
  templateUrl: './ig-hashtag.component.html',
  styleUrls: ['./ig-hashtag.component.scss']
})
export class IgHashtagComponent implements OnInit {
  igHashtagFull: IgHashtagFull;
  mediaOpen = false;

  constructor(public route: ActivatedRoute, public mediaService: IgMediaService,
    private modalService: ModalService) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.igHashtagFull = data.igHashtagFull;
    });
  }

  downloadThumbmail(media: IgMedia) {
    this.mediaService.GetMediaImageByUrl(media.thumbnail_src);
  }

  async openMedia(media: IgMedia) {
    if(this.mediaOpen) return;

    this.mediaOpen = true;
    const fullMedia = await this.mediaService.GetIgMediaFullByShortcode(media.shortcode).toPromise();
    const modal = this.modalService.open(FullMediaComponent, { centered: true });
    modal.result.finally(() => this.mediaOpen = false);
    modal.componentInstance.fullMedia = fullMedia;
  }
}
