import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './components/home/home.component';
import { igUserRoutes } from './components/ig-user/ig-user.routes';
import { igMediaRoutes } from './components/ig-media/ig-media.routes';
import { igHashtagRoutes } from './components/ig-hashtag/ig-hashtag.routes';


const routes: Routes = [{ path: '', component: HomeComponent }, ...igUserRoutes, ...igMediaRoutes, ...igHashtagRoutes];

@NgModule({
	imports: [RouterModule.forRoot(routes)],
	exports: [RouterModule]
})
export class AppRoutingModule { }
