import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { IgMediaFull, IgMediaType } from 'src/app/models/ig-media-model';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { IgMediaService } from '../../ig-media/ig-media.service';

@Component({
  selector: 'app-full-media',
  templateUrl: './full-media.component.html',
  styleUrls: ['./full-media.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class FullMediaComponent implements OnInit {
  @Input() fullMedia: IgMediaFull;
  currentImageShowing = 0;

  constructor(public activeModal: NgbActiveModal, private mediaService: IgMediaService) { }

  ngOnInit(): void { }

  DownloadCurrentMedia() {
    if(this.fullMedia.type !== IgMediaType.GraphVideo)
      this.mediaService.GetMediaImageByUrl(this.GetCurrentImage());
  }

  GetCurrentImage() {
    if(this.fullMedia.type === IgMediaType.GraphImage)
      return this.fullMedia.display_url;
    else if(this.fullMedia.type === IgMediaType.GraphSidecar)
      return this.fullMedia.media_list[this.currentImageShowing];
  }

  NextImage() {
    if(this.fullMedia.media_list.length === this.currentImageShowing + 1) {
      this.currentImageShowing = 0;
      return this.fullMedia.media_list[this.currentImageShowing];
    } else {
      this.currentImageShowing++;
      return this.fullMedia.media_list[this.currentImageShowing];
    }
  }

  PreviousImage() {
    if(this.currentImageShowing - 1 < 0) {
      this.currentImageShowing = this.fullMedia.media_list.length - 1;
      return this.fullMedia.media_list[this.currentImageShowing];
    } else {
      this.currentImageShowing--;
      return this.fullMedia.media_list[this.currentImageShowing];
    }
  }

  Close(reason) {
    this.activeModal.close(reason);
  }

  Dismiss(reason) {
    this.activeModal.dismiss(reason);
  }

}
