import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { IgMediaFull } from 'src/app/models/ig-media-model';

@Injectable()
export class IgMediaService {
  URL = "/api/ig-media/";

  constructor(private http: HttpClient) { }

  GetIgMediaFullByShortcode(shortcode: string): Observable<IgMediaFull> {
    return this.http.get<IgMediaFull>(this.URL + shortcode).pipe(
      map(res => Object.assign(new IgMediaFull(), res)));
  }

  GetMediaImageByUrl(full_url: string): void {
    this.http.get(full_url, { responseType: 'blob' }).subscribe(val => {
      let url = URL.createObjectURL(val);
      this.downloadUrl(url, 'image.png');
      URL.revokeObjectURL(url);
    });
  }

  private downloadUrl(url: string, fileName: string) {
    let a: any = document.createElement('a');
    a.href = url;
    a.download = fileName;
    document.body.appendChild(a);
    a.style = 'display: none';
    a.click();
    a.remove();
  }
}