import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { IgHashtagFull } from 'src/app/models/ig-hashtag-model';

@Injectable()
export class IgHashtagService {
  URL = "/api/ig-hashtag/";
  constructor(private http: HttpClient) { }

  GetIgHashtagFullByHashtag(hashtag: string): Observable<IgHashtagFull> {
    return this.http.get<IgHashtagFull>(this.URL + hashtag).pipe(
      map(res => Object.assign(new IgHashtagFull(), res)));
  }
}