import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot, Routes } from '@angular/router';
import { Observable } from 'rxjs';
import { IgUserFull } from 'src/app/models/ig-user-model';
import { IgUserComponent } from './ig-user.component';
import { IgUserService } from './ig-user.service';
const PREFIX = '';

@Injectable({ providedIn: 'root' })
export class IgUserFullResolver implements Resolve<IgUserFull> {
	constructor(private service: IgUserService) { }

	resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<IgUserFull> {
		return this.service.GetIgUserByUsername(route.paramMap.get('username'));
	}
}

export const igUserRoutes: Routes = [
	{
		path: PREFIX + ':username', 
		component: IgUserComponent,
		resolve: {
			igUserFull: IgUserFullResolver
		}
	}
];