import { IgUser } from "./ig-user-model";
import { IgHashtag } from "./ig-hashtag-model";
import { isNullOrUndefined } from 'util';

export class IgTopSearch {
    users: IgUser[] = [];
    places: [] = [];
    hashtags: IgHashtag[] = [];
    orderedMixedList: any[] = [];

    public constructor(obj?: any) {
        Object.assign(this, obj);
        this.users = this.users.map(x => this.AddElementConverted(IgUser, x));
        this.hashtags = this.hashtags.map(x => this.AddElementConverted(IgHashtag, x));
    }

    public AddElementConverted<T>(type: (new () => T), obj: any): T {
        const el = Object.assign(new type(), obj);
        this.AddToMixedList(el);
        return el;
    }

    AddToMixedList(element): void {
        if(!isNullOrUndefined(element) && !isNullOrUndefined(element.position)) {
            if(this.orderedMixedList.length === 0) {
                this.orderedMixedList.push(element);
            } else {
                let index = 0;
                let added = false;
                while(index < this.orderedMixedList.length) {
                    if(element.position <= this.orderedMixedList[index].position) {
                        this.orderedMixedList.splice(index, 0, element);
                        added = true;
                        break;
                    }

                    index++;
                }

                if(!added) {
                    this.orderedMixedList.push(element);
                }
            }
        }
    }
}